<template>
  <div class="container">
    <div class="row">
      <h4>Tabella processi</h4>
      <table class="table table-sm table-striped">
        <thead class>
          <tr>
            <th>Percorso script</th>
            <th>Nome script</th>
            <th>Data inizio</th>
            <th>Data fine</th>
          </tr>
        </thead>
        <tbody>
          <tr
            style="cursor: pointer"
            v-for="processo in processi"
            :key="processo.ID"
          >
            <td scope="row">{{ processo.path_script }}</td>
            <td scope="row">{{ processo.script_name }}</td>
            <td scope="row">{{ processo.date_start }}</td>
            <td scope="row">{{ processo.date_end }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      processi: new Array(),
    };
  },
  mounted() {
    this.$store.dispatch("loading");
    this.axios
      .get(this.apiURL + "/monitor/all")
      .then((r) => {
        this.processi = r.data;
      })
      .then(() => this.$store.dispatch("loaded"));
  },
};
</script>